@import "../../common/styles/slider-template";
@import "../../common/styles/popup-button";
@import "../../common/styles/components-notice";

.elementor-widget-coca_bais_before_after_image_slider {
	display: block;
	@include slider-template;
	@include popup-button;

	.coca-bais-container {
		display: block;
		opacity: 1 !important;

		&:not(.coca-image-compare-container) img:not(:first-child) {
			display: none;
		}

		img {
			border: none;
			border-radius: 0;
			box-shadow: none;
		}

		.coca-image-compare-after-label:before,
		.coca-image-compare-before-label:before {
			min-width: fit-content;
		}
	}

	.components-notice {
		@include components-notice;
	}
}

body.elementor-editor-active .coca-bais-shortcode .coca-bais-container {
	display: block !important;
	opacity: 1 !important;

	&:has(.popup-button) {
		position: relative;

		.popup-button {
			position: absolute;
			right: 10px;
			bottom: 10px;
			z-index: 999;
			padding: 5px;
			line-height: 0;
			cursor: pointer;
			background-color: #fff;
			border-radius: 5px;
		}
	}

	img:not(:first-child) {
		display: none;
	}
}
