// Class Prefix
$pluginPrefix: "coca-image-compare" !default;


@mixin popup-button() {
	position: relative;

	.popup-button {
		position: absolute;
		right: 10px;
		bottom: 10px;
		z-index: 999;
		padding: 5px;
		line-height: 0;
		cursor: pointer;
		background-color: #fff;
		border-radius: 5px;

		&.bottom-left {
			right: auto;
			left: 10px;
		}

		&.top-left {
			top: 10px;
			right: auto;
			bottom: auto;
			left: 10px;
		}

		&.top-right {
			top: 10px;
			right: 10px;
			bottom: auto;
		}

		&:hover,
		&:focus,
		&:active {
			background: rgba(234, 84, 0, 1);

			svg {
				color: #fff;
			}
		}

		&,
		& svg {
			transition: 300ms ease-in-out;
		}

		svg {
			pointer-events: none;
		}
	}
}
