// Class Prefix
$pluginPrefix: "coca-image-compare" !default;


@mixin slider-template() {
	.#{$pluginPrefix}-left-arrow,
	.#{$pluginPrefix}-right-arrow,
	.#{$pluginPrefix}-down-arrow,
	.#{$pluginPrefix}-up-arrow {
		transition: 300ms ease-in-out;
	}

	.template-style01 {
		&.coca-image-compare-wrapper.#{$pluginPrefix}-horizontal .#{$pluginPrefix}-handle,
		&.coca-image-compare-wrapper.#{$pluginPrefix}-vertical .#{$pluginPrefix}-handle {
			--coca-image-compare-handle-triangle-position: 5px;
		}

		&.#{$pluginPrefix}-horizontal {
			.#{$pluginPrefix}-after-label::before,
			.#{$pluginPrefix}-before-label::before {
				top: 40px;
				margin-top: 0;
			}

			.#{$pluginPrefix}-before-label::before {
				left: 40px;
			}

			.#{$pluginPrefix}-after-label::before {
				right: 40px;
			}

			.#{$pluginPrefix}-handle {
				--coca-image-compare-handle-radius: 0;
				transform: rotate(45deg);

				&::before,
				&::after {
					width: 0 !important;
				}
			}

			.#{$pluginPrefix}-left-arrow {
				top: 78%;
				left: 49%;
				transform: rotate(316deg);
			}

			.#{$pluginPrefix}-right-arrow {
				top: 22%;
				right: 51%;
				transform: rotate(316deg);
			}
		}

		&.#{$pluginPrefix}-vertical {
			.#{$pluginPrefix}-after-label::before,
			.#{$pluginPrefix}-before-label::before {
				margin-top: 0;
			}

			.#{$pluginPrefix}-before-label::before {
				top: 40px;
			}

			.#{$pluginPrefix}-after-label::before {
				bottom: 40px;
			}

			.#{$pluginPrefix}-handle {
				border-radius: 0;
				transform: rotate(45deg);

				&::before,
				&::after {
					width: 0 !important;
				}
			}

			.#{$pluginPrefix}-down-arrow {
				bottom: 105%;
				left: 25%;
				transform: rotate(136deg);
			}

			.#{$pluginPrefix}-up-arrow {
				top: 105%;
				left: 75%;
				transform: rotate(136deg);
			}
		}
	}

	.template-style02,
	.template-style03 {
		&.#{$pluginPrefix}-horizontal {
			.#{$pluginPrefix}-after-label::before,
			.#{$pluginPrefix}-before-label::before {
				top: 80%;
				padding: 16px 30px;
				margin-top: 0;
				line-height: 1.5;
			}
		}

		&.#{$pluginPrefix}-vertical {
			.#{$pluginPrefix}-after-label::before,
			.#{$pluginPrefix}-before-label::before {
				right: 0;
				left: auto;
				padding: 16px 30px;
				margin-top: 0;
				margin-left: 0;
				line-height: 1.5;
			}
		}
	}

	.template-style02,
	.template-style03,
	.template-style04 {
		&.#{$pluginPrefix}-horizontal {
			.#{$pluginPrefix}-before-label::before {
				left: 0;
				border-top-right-radius: 50px;
				border-bottom-right-radius: 50px;
			}

			.#{$pluginPrefix}-after-label::before {
				right: 0;
				border-top-left-radius: 50px;
				border-bottom-left-radius: 50px;
			}
		}

		&.#{$pluginPrefix}-vertical {
			.#{$pluginPrefix}-before-label::before,
			.#{$pluginPrefix}-after-label::before {
				border-top-left-radius: 50px;
				border-bottom-left-radius: 50px;
			}

			.#{$pluginPrefix}-before-label::before {
				top: 10%;
			}

			.#{$pluginPrefix}-after-label::before {
				bottom: 10%;
			}
		}

	}

	.template-style02,
	.template-style05 {
		&.#{$pluginPrefix}-horizontal {
			.#{$pluginPrefix}-left-arrow {
				border-right-color: #737373;
			}

			.#{$pluginPrefix}-right-arrow {
				border-left-color: #737373;
			}
		}

		&.#{$pluginPrefix}-vertical {
			.#{$pluginPrefix}-down-arrow {
				border-top-color: #737373;
			}

			.#{$pluginPrefix}-up-arrow {
				border-bottom-color: #737373;
			}
		}
	}

	.template-style02 {
		&.#{$pluginPrefix}-horizontal .#{$pluginPrefix}-handle {
			--coca-image-compare-handle-circle-width: 24px;
			--coca-image-compare-handle-circle-height: 120px;
			--coca-image-compare-handle-stroke: 1px;
			background: #fff;
		}

		&.#{$pluginPrefix}-vertical .#{$pluginPrefix}-handle {
			--coca-image-compare-handle-circle-width: 120px;
			--coca-image-compare-handle-circle-height: 24px;
			--coca-image-compare-handle-stroke: 1px;
			background: #fff;
		}
	}

	.template-style03 {
		.#{$pluginPrefix}-handle {
			--coca-image-compare-handle-circle-width: 85px;
			--coca-image-compare-handle-circle-height: 85px;
		}
	}

	.template-style04 {
		.#{$pluginPrefix}-handle {
			border: none;
			box-shadow: none;
		}

		&.#{$pluginPrefix}-horizontal {
			.#{$pluginPrefix}-handle {

				&::before {
					margin-bottom: 0;
					margin-left: 0 !important;
				}

				&::after {
					margin-top: 0;
					margin-left: 0 !important;
				}

				.#{$pluginPrefix}-left-arrow {
					margin-left: -15px;
				}

				.#{$pluginPrefix}-right-arrow {
					margin-right: -18px;
				}
			}

			.#{$pluginPrefix}-after-label::before,
			.#{$pluginPrefix}-before-label::before {
				top: 10%;
				padding: 16px 30px;
				margin-top: 0;
				line-height: 1.5;
			}
		}

		&.#{$pluginPrefix}-vertical {
			.#{$pluginPrefix}-handle {

				&::before {
					margin-top: 0;
					margin-bottom: 0;
					margin-left: 0;
				}

				&::after {
					margin-top: 0;
					margin-right: 0;
					margin-left: 0;
				}

				.#{$pluginPrefix}-down-arrow {
					margin-bottom: -22px;
				}

				.#{$pluginPrefix}-up-arrow {
					margin-top: -17px;
				}
			}

			.#{$pluginPrefix}-after-label::before,
			.#{$pluginPrefix}-before-label::before {
				left: 0;
				padding: 16px 30px;
				margin-top: 0;
				margin-left: 0;
				line-height: 1.5;
				border-radius: 0 50px 50px 0;
			}

			.#{$pluginPrefix}-before-label::before {
				top: 10%;
			}

			//.#{$pluginPrefix}-after-label::before { }
		}
	}

	.template-style05 {
		.#{$pluginPrefix}-handle {
			--coca-image-compare-handle-stroke: 10px;
			background-color: #fff;
			-webkit-background-clip: padding-box;
			background-clip: padding-box;
			border-color: rgba(255, 255, 255, 0.53);
			border-style: solid;
		}

		&.#{$pluginPrefix}-horizontal:not(.handle-bottom) .#{$pluginPrefix}-handle {

			&:before {
				margin-bottom: calc(calc(var(--coca-image-compare-handle-circle-height) / 2) - 1px);
			}

			&:after {
				margin-top: calc(var(--coca-image-compare-handle-circle-height) / 2 - 1px);
			}
		}

		&.#{$pluginPrefix}-vertical:not(.handle-bottom) .#{$pluginPrefix}-handle {

			&:before {
				margin-left: calc(var(--coca-image-compare-handle-circle-width) / 2 - 1px);
			}

			&:after {
				margin-right: calc(var(--coca-image-compare-handle-circle-width) / 2 - 1px);
			}
		}
	}

	.template-style06 {
		&.#{$pluginPrefix}-horizontal .#{$pluginPrefix}-handle {
			--coca-image-compare-handle-circle-height: 70px;
			--coca-image-compare-handle-circle-width: 15px;
			--coca-image-compare-handle-triangle-position: 15px;
		}

		&.#{$pluginPrefix}-vertical .#{$pluginPrefix}-handle {
			--coca-image-compare-handle-circle-width: 70px;
			--coca-image-compare-handle-circle-height: 15px;
			--coca-image-compare-handle-triangle-position: 15px;
		}
	}
}
